* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell,
    'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

.wrapper {
  display: flex;
  align-items: center;
}

.title {
  margin-left: 30px;
  font-size: 40px;
}

.item {
  font-size: 24px;
  color: darkblue;
}

.value {
  font-weight: bold;
  font-size: 20px;
  margin-left: 20px;
  color: #212121;
}

.list {
  display: flex;
  align-items: center;
}

.flag {
  margin-right: 30px;
}
